<template>
  <div class="home-container">
    <div class="logo-container">
      <img src="https://msf.or.kr/sites/all/themes/msfkorea/images/logo.svg" alt="로고 이미지">
    </div>
    <div class="phone-input">
      <label class="phone-label" for="phone">휴대폰 번호 조회</label>
      <div class="input-button-container">
        <input
          type="tel"
          id="phone"
          v-model="inputPhoneNumber"
          @input="handleInput"
          placeholder="숫자만 입력해주세요"
        >
        <button @click="showConfirmationDialog = true">조회</button>
      </div>
      <div v-if="showConfirmationDialog" class="confirmation-dialog">
      <p>입력하신 번호가 맞습니까?</p>
      <p style="height:30px;">{{ formatPhoneNumber(inputPhoneNumber) }}</p>
      <div class="confirmation-buttons">
        <button @click="findPhone">확인</button>
        <button @click="cancelFind">취소</button>
      </div>
    </div>
    </div>
  </div>
</template>

<script>
import data from "@/assets/data.json";

export default {
  data() {
    return {
      inputPhoneNumber: "",
      list: data,
      showConfirmationDialog: false
    };
  },
  methods: {
    handleInput() {
      this.inputPhoneNumber = this.inputPhoneNumber.replace(/[^0-9]/g, "");
    },
    findPhone() {
      const phoneNumberToFind = this.inputPhoneNumber;
      const foundItem = this.list.find(item => item.phone === phoneNumberToFind);
      if (foundItem) {
        this.$router.push(`/check?phone=${encodeURIComponent(foundItem.phone)}`);
      } else {
        this.$router.push(`/fail`);
      }
    },
    cancelFind() {
      this.inputPhoneNumber = "";
      this.showConfirmationDialog = false;
    },
    formatPhoneNumber(phoneNumber) {
    const cleaned = ('' + phoneNumber).replace(/\D/g, '');
    const match = cleaned.match(/^(\d{3})(\d{4})(\d{4})$/);
    if (match) {
      return match[1] + ' ' + match[2] + ' ' + match[3];
    }
    return phoneNumber;
  }
  },
};
</script>

<style scoped>
.home-container {
  height: 100vh;
  text-align: center;
  padding: 20px;
}

.logo-container {
  text-align: center;
  margin: 40px 0;
}

.logo-container img {
  max-width: 180px;
  height: auto;
}

.phone-input {
  margin-top: 60px;
}

.phone-label {
  display: inline-block;
  width: 300px;
  height: 30px;
  padding-bottom: 10px;
  text-align: left;
  font-size: 22px;
  font-weight: bold;
  font-family: 'Arial', sans-serif; /* 세련된 폰트를 사용하도록 변경 */
}

.input-button-container {
  display: flex;
  align-items: center;
  justify-content: center;
}

input[type="tel"] {
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
  outline: none;
}

button {
  margin-left: 8px;
  padding: 8px 16px;
  background-color: red;
  color: white;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  cursor: pointer;
}

button:hover {
  background-color: #ccc;
}

.confirmation-dialog {
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  border: 1px solid #ccc;
  border-radius: 8px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  padding: 20px;
  min-width: 240px;
  max-width: 300px;
  text-align: center;
  z-index: 1;
}

.confirmation-content {
  margin-bottom: 16px;
}

.confirmation-buttons button {
  background-color: red;
  color: white;
  margin: 0 8px;
  padding: 8px 16px;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.confirmation-buttons button:hover {
  background-color: #ccc;
}



</style>
