import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import check from '@/components/check'
import fail from '@/components/fail'
import error from '@/components/error'


Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/check',
      name: 'check',
      component: check
    },
    {
      path: '/fail',
      name: 'fail',
      component: fail
    },
    {
      path: '/error',
      name: 'error',
      component: error
    }
  ]
})
