<template>
  <div id="app">
    <router-view/>
    <div v-if="!isHomeRoute" @click="redirectToHome" style="width: 100px; height: 30px; line-height:30px; margin:40px auto; cursor:pointer; background-color:red; color:#fff;">돌아가기</div>
    <div class="reset" @click="showReset=!showReset">#</div>
    <p @click="complete" class="reset-btn" v-if="showReset">Reset</p>
  </div>
</template>

<script>
export default {
  name: 'App',
  data() {
    return {
      showReset : false
    }
  },
  computed: {
    isHomeRoute() {
      return this.$route.path === '/';
    }
  },
  methods: {
    redirectToHome() {
      this.$router.push('/')
    },
    complete() {
      this.showReset = false
      localStorage.removeItem('phones');
      alert("초기화 되었습니다.")
    }
  }
}
</script>

<style>
  *{
    margin: 0;
    padding: 0;
  }
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  position: relative;
}
.reset:hover {
    opacity: 1;
}
.reset {
  cursor: pointer;
  position: absolute;
  left:10px;
  top:0;
  opacity: 0;
} 

.reset-btn {
  position: absolute;
  left:20px;
  top:10px;
  cursor:pointer;
}
</style>
