<template>
  <div>
    <h3>후원자님 안녕하세요</h3>
    <p v-if="this.$route.query.phone">후원자님 안녕하세요</p>

    <p v-if="duplicate">이미 조회한 번호입니다.</p>
  </div>
</template>

<script>
export default {
  data() {
    return {
      duplicate : false
    };
  },
  mounted() {
    const storedPhone = localStorage.getItem('phones');
    const queryPhone = this.$route.query.phone;

    if (queryPhone) {
      let phones = storedPhone ? JSON.parse(storedPhone) : [];
      
      if (!phones.includes(queryPhone)) {
        phones.push(queryPhone);
        localStorage.setItem('phones', JSON.stringify(phones));
      } else {
        this.duplicate = true;
      }
    }
  },
};
</script>
