<template>
  <div>
    <h1>check</h1>
    <div>
      <label for="phone">핸드폰 번호:</label>
      <input type="text" id="phone" v-model="inputPhoneNumber">
      <button @click="findName">조회</button>
    </div>
    <!-- <ul>
      <li v-for="(item, index) in list" :key="index">{{ item }}</li>
    </ul> -->
    <p v-if="foundName">일치하는 이름: {{ foundName }}</p>
    <p v-else>해당 핸드폰 번호에 대한 이름을 찾을 수 없습니다.</p>
  </div>
</template>

<script>
import data from "@/assets/data.json";
export default {
  data() {
    return {
      list: [],
      inputPhoneNumber: "",
      foundName: null,
    };
  },
  created() {
    this.list = data;
  },
  methods: {
    findName() {
      const phoneNumberToFind = this.inputPhoneNumber;
      const foundItem = this.list.find(item => item.phone === phoneNumberToFind);
      if (foundItem) {
        this.foundName = foundItem.name;
      } else {
        this.foundName = null;
      }
    },
  },
};
</script>
